a {
  color: #334CD5; }

a:link,
a:visited {
  color: #3B55E6; }

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  background: #C10E21; }

.site-title a:hover,
.site-title a:active {
  color: #3B55E6; }

.sidebar-navigation-toggle:hover {
  color: #3B55E6; }

.main-navigation-menu a:hover,
.main-navigation-menu a:active {
  color: #3B55E6; }

.main-navigation-menu ul {
  background: #C10E21; }

.header-social-icons .social-icons-menu li a:hover {
  color: #3B55E6; }

.widget-title a:link,
.widget-title a:visited {
  color: #3B55E6; }

.widget-magazine-posts .widget-header .widget-title {
  color: #C10E21; }

.entry-title a:hover,
.entry-title a:active {
  color: #C10E21; }

/* Read more Link */
.more-link {
  background: #C10E21; }

.entry-categories .meta-category a {
  background: #C10E21; }

.entry-categories .meta-category a:hover,
.entry-categories .meta-category a:active {
  color: #E11127; }

.widget_tag_cloud .tagcloud a,
.entry-tags .meta-tags a {
  background: #334CD5; }

.post-navigation .nav-links a:hover,
.post-navigation .nav-links a:active {
  background: #E11127; }

.page-header .archive-title {
  color: #C10E21; }

.pagination a:hover,
.pagination .current {
  background: #E11127; }

.infinite-scroll #infinite-handle span:hover {
  background-color: #E11127; }

.comments-header .comments-title,
.comment-reply-title span {
  border: 3px solid #C10E21;
  color: #C10E21; }

.reply .comment-reply-link:hover,
.reply .comment-reply-link:active {
  background: #E11127; }

@media only screen and (max-width: 60em) {
  .main-navigation-menu .submenu-dropdown-toggle:hover:before,
  .main-navigation-menu .submenu-dropdown-toggle:active:before {
    color: #C10E21; } }

.custom-contact .col-sm-6 {
  width: 50%; }

.copyright-divider .list-unstyled {
  list-style: none;
  padding-left: 0; }

.sidebar-navigation {
  padding: 0; }
  .sidebar-navigation .menu-item {
    border-bottom: 1px solid #dddddd; }
  .sidebar-navigation .sub-menu .menu-item {
    border-bottom: 1px solid #E11127; }

@media only screen and (min-width: 60em) {
  .site-header {
    display: none; } }

.front-page-login-form .Grid-cell-liner {
  border: solid 1px #cacaca; }

.front-page-login-form .login-header {
  margin-top: 0; }

.front-page-login-form .login-form-password-label {
  margin-top: .5em; }

.front-page-login-form form[name="login-form"] label {
  display: block; }

.front-page-login-form form[name="login-form"] input[type="submit"] {
  margin: 1em 0; }

.front-page-login-form .log-in-with-sso-forward,
.front-page-login-form .log-in-with-sso {
  display: inline-block; }

.starter-maps-grid .caption {
  margin-top: .2em;
  display: inline-block; }

/**** AUTOCOMPLETE ***/
.ui-autocomplete {
  border: 1px solid #6D6E71;
  border-radius: 3px;
  font-family: inherit; }

ul.ui-autocomplete {
  background-color: #FFFFFF;
  max-width: 300px; }

ul.ui-autocomplete li {
  padding: 8px 4px; }

ul.ui-autocomplete li:hover {
  background-color: #C10E21;
  color: white; }

.ui-autocomplete-input {
  min-width: 60px; }

tr.priority_staff_save td {
  padding-bottom: 24px; }

a.submit_staff_partners {
  font-size: 1.5em;
  font-weight: bold; }

.close {
  vertical-align: super; }
